<template>
   <section class="section">
       <div class="my">
           <section class="my-head">
               <div class="icon">
                   <span></span>
                   <span></span>
               </div>
               <div class="info">
                   <dl>
                       <dt><img src="../../assets/images/phone.png" alt=""></dt>
                       <dd>
                           <p>{{phone}}</p>
                           <p>...</p>
                       </dd>
                   </dl>
               </div>
           </section>
           <section class="my-nav">
               <div class="list">
                    <dl>
                        <dt><img src="../../assets/images/cart.png" alt=""></dt>
                        <dd></dd>
                    </dl>
                    <dl>
                        <dt><img src="../../assets/images/quan.png" alt=""></dt>
                        <dd><span>1</span></dd>
                    </dl>
                    <dl>
                        <dt><img src="../../assets/images/money.png" alt=""></dt>
                        <dd><span>182</span></dd>
                    </dl>
                    <dl>
                        <dt><img src="../../assets/images/gift.png" alt=""></dt>
                        <dd><span>0</span></dd>
                    </dl>
               </div>
                <a href="javascript:void(0);" @click="show=!show"> ></a>
                <!-- <mt-button @click.native="handleClick"> ></mt-button> -->
                
           </section>
           <section class="my-task">
              <h3></h3> 
                 <div class="miss">
                    <dl>
                        <dt><img src="../../assets/images/sign.png" alt=""></dt>
                        <dd>
                            <p></p>
                            <p>,7!</p>
                        </dd>
                    </dl>
                    <button @click="sign" class="btn"></button>
                     
               </div>
               <div class="miss">
                    <dl>
                        <dt><img src="../../assets/images/book.png" alt=""></dt>
                        <dd>
                            <p> <span>20</span></p>
                            <p>60</p>
                        </dd>
                    </dl>
                    <p class="op">0%<span></span></p>
                   
               </div>
           </section>
           <section class="my-detail">
               <div class="main">
                   <div class="bottom">
                       <dl>
                           <dt><img src="../../assets/images/buy.png" alt=""></dt>
                           <dd></dd>
                       </dl>
                       <p>12</p>
                   </div>
                   <div class="bottom">
                       <dl>
                           <dt><img src="../../assets/images/yueli.png" alt=""></dt>
                           <dd></dd>
                       </dl>
                       <p>42</p>
                   </div>
                   <div class="bottom">
                       <dl>
                           <dt><img src="../../assets/images/note.png" alt=""></dt>
                           <dd></dd>
                       </dl>
                   </div>
                  
               </div>
           </section>
           <transition name="fade">
               <section class="my-msg" v-show="show">
               <div class="alertMsg">
                   <h3></h3>
                   <div class="del">
                       <dl>
                           <dt>1</dt>
                           <dd>100</dd>
                       </dl>
                       <dl>
                           <dt>5</dt>
                           <dd>500</dd>
                       </dl>
                       <dl>
                           <dt>10</dt>
                           <dd>1,000</dd>
                       </dl>
                       <dl>
                           <dt>20</dt>
                           <dd>2,000</dd>
                       </dl>
                       <dl>
                           <dt>5</dt>
                           <dd>5,000</dd>
                       </dl>
                       <dl>
                           <dt>100</dt>
                           <dd>10,000</dd>
                       </dl>
                   </div>
                   <p><span></span><span>></span></p>
                   <button @click="cancel"></button>
               </div>
           </section>
           </transition>   
       </div>

       <!--  <mt-swipe :auto="4000" v-if="1">
            <mt-swipe-item class="slide">1</mt-swipe-item>
            <mt-swipe-item class="slide">2</mt-swipe-item>
            <mt-swipe-item class="slide">3</mt-swipe-item>
        </mt-swipe> -->

        
   </section>
</template>

<script>
import axios from "axios";

import {mapState,mapActions} from "vuex";

import { toast } from 'mint-ui';

import "../utils/swiper-3.3.1.min.js";

export default {
   data(){
       return{
           phone:localStorage.getItem("name"),
           show:false,
           asign:""
       }
   },
    computed:{
        ...mapState([
            "word",
        ])
    },
    methods:{
        	recharge(){
                if(this.phone){
                    this.show = true;
                }else{
                    window.location.href = "http://localhost:7000/#/index/exit";
                }
            },
            cancel(){
                this.show = false;
            },
            sign(){
                console.log(2222);
                var date = new Date();
                date.setTime(date.getTime()+24*60*60*1000);
                document.cookie = "asign=true;expire="+date.toGMTString();
                var btn = document.querySelector(".btn");
                btn.innerHTML = "";
                btn.style.color = "#999999";
                btn.disabled = true;

            }
            
    },
    mounted(){
        this.asign=document.cookie.split(";")[0].split("=")[1];
        if(this.asign){
            var btn = document.querySelector(".btn");
            btn.innerHTML = "";
            btn.style.color = "#999999";
            btn.disabled = true;
        }
    }
    
}
</script>

