<template>
   <section class="login">
        <div class="login-title">
            <h1></h1>
            <h3>MiReader</h3>
        </div>  
        <div class="login-inp">
            <input type="text" placeholder="" v-model="phone">
            <input type="password" placeholder="" v-model="pwd">
            <router-link :to="{name:'forget'}" class="forget"></router-link>
            <a class="sub" @click="login"></a>
            <!-- <router-link :to="n?{name:'home'}:{name:'exit'}" class="sub" v-model="pwd" @click.native="submit"></router-link> -->
            <router-link :to="{name:'register'}" class="reg"></router-link>
        </div>
   </section>
</template>

<script>
import {mapState,mapActions } from "vuex";
import axios from "axios";
export default {
    data(){
        return{
            phone:"",
            pwd:"",
            list:[],
            n:false
        }
    },
   
    methods:{
       
        login(){
            axios.post("/findUser",{
                phone:this.phone
            }).then(res=>{
                this.list = res.data;
                if(this.pwd==this.list.pwd){
                    localStorage.setItem("name",this.phone);
                    window.location.href = "/#/index/home"
                }else{
                    alert("");
                    window.location.href = "/#/index/exit"
                    
                }
            });
        }
    },
    mounted(){
        localStorage.removeItem("name");
    }
  
}
</script>

<style scopde>
    
</style>



