
//  Vuex  store  mutation
//  mutation   (type)    (handler)

//  store  action  commit 

//  mutation  

export default {
    CHANGE(state){   // vuex state  changeMyCount  actionType
        console.log("003  mutations  type  change state");
        console.log(state);
        state.myCount +=100;
    },
    MYCOUNTADD(state){
        console.log("003  mutations  type  change state");
        state.myCount +=200;        
    },
    DESCBYNUM(state){
        state.myCount-=300;
    },
    changeAddress(state,preload){
        state.address = preload;
    },
    addByInp(state,preload){
        state.myCount += preload;
    },
    changeNavIdx(state,id){
        state.navIndex = id;
    },
    changeword(state,data){
        state.word = data;
    },
    getBanner(state,data){
        state.banner = data;
    },
    CHANGETXT(state){
        state.txt=""
    }

}


