<template>
    <div class="detail">
      <mt-header :title="title" class="head">
            <div @click="goback" slot="left">
                <mt-button icon="back"></mt-button>
            </div>
        </mt-header>
        <div class="info">
            <img :src="books.cover" alt="">
            <div class="cont">
                <h2>:&nbsp;{{books.authors}}</h2>
                <h3>:&nbsp;{{books.rights}}</h3>
                <h3>:&nbsp;{{books.word_count}}</h3>
                <h3>:&nbsp;{{books.price}}</h3>
            </div>  
        </div>
        <p>{{books.summary}}</p>
        <div class="foot">
            <button @click="addto" :class="'add'">+ </button>
            <button></button>
            <button></button>
        </div>
    </div>
</template>

<script>
import router from "./router.js";
import axios from "axios";
export default { 
    data(){
        return {
          books:"",
          arr:[],
          info:"",
          other:""
        }
    },
    computed:{
        title(){
            return this.$route.query.name;
        }
        
    },
    methods:{
        goback(){
            router.go(-1);
        },
        addto(){
           var name = this.$route.query.name;
           console.log(name);
           console.log(this.books.cover)
           var add = document.querySelector(".add");
           console.log(add);
            axios.get("/insert",{
                params:{
                    name,
                    cover:this.books.cover
                    }
                });
                add.innerHTML = "";
                add.style.color = "grey";
           
        }
        
        
    },
    mounted(){
        var name = this.$route.query.name;
        //
        var add = document.querySelector(".add");
       /*  axios.get("/findAll").then(res=>{
            this.other =res.data;
        }).then(res=>{
            for(var i in this.other){
                if(name==this.other[i].title){
                    add.innerHTML = "";
                    add.style.color = "grey";
                }
            }
        }) */

        this.$http.get("/Novdetail",{
            params:{
                name
            }
        }).then(res=>{
            this.books =res.data;
            console.log(this.books);
        })
          
        
    }
}   
</script>

<style scoped>
    
</style>



